@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {

  --main-laranja-color: #f58634;

  --main-laranja-claro-color: #fbbd89;

  --main-dourado-color: #997c30;

  --main-bg-color: rgb(238, 223, 204);

}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body {
  background-color: rgb(238, 223, 204) !important;
  background-color: var(--main-bg-color) !important;
}

.main-content {
  background-color: rgb(238, 223, 204);
  background-color: var(--main-bg-color);
  display: flex;
  flex-direction: column;
  margin: 52px 0 50px;
}

.app-footer {
  bottom: 0;
  position: fixed;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 10px 0 0;
}

.card-body-recipies {
  align-items: center;
  background: white;
  border-radius: 50px 50px 6px 6px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  height: 145px;
  margin: 80px 5px 10px;
  width: 161px;
}

.card-body-recipies div {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
}

.card-body-recipies img {
  border: 3px solid white;
  border-radius: 60px;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  height: 120px;
  margin: -70px 0 0;
  width: 120px;
}

.card-body-recipies h4 {
  color: #997c30;
  color: var(--main-dourado-color);
  font-family: 'Lobster', cursive;
  height: 80px;
  letter-spacing: 1px;
  margin: 5px;
  overflow: hidden;
  text-align: center;
  text-overflow: ellipsis;
}

.recipes-done-cards {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.recipes-done-card {
  background: white;
  border: 1px solid rgba(248, 237, 237, 1);
  border-radius: 6px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  box-sizing: border-box;
  display: flex;
  height: 143px;
  margin: 5px 0;
  width: 330px;
}
.recipes-done-card div {
  padding: 5px;
}

.recipes-done-card h5 {
  font-size: 0.8rem;
  font-weight: 700;
  margin-bottom: 0;
}

.recipes-done-card h4 {
  color: #997c30;
  color: var(--main-dourado-color);
  font-family: 'Lobster', cursive;
  font-size: 1.5rem;
  margin: 3px 0;
}

.recipes-done-card p {
  font-size: 0.8rem;
  font-weight: 500;
  margin: 0;
}

.recipes-done-card-img {
  border-radius: 6px 0 0 6px;
  height: 143px;
  margin: 0 5px 0 0;
  width: 165px;
}

.recipes-done-card .favorite-button {
  background-color: white;
  border: none;
  height: 20px;
  margin: 0 7px;
  width: 20px;
  position: relative;
  top: 50px;
  left: 83px;
}

.recipes-done-card button {
  background-color: white;
  border: none;
  height: 20px;
  margin: 0 7px;
  width: 20px;
  position: relative;
  top: -65px;
  left: 113px;
}

.recipes-done-card-share {
  height: 17px;
  margin: 2px;
  width: 17px;
}

.recipes-done-tags {
  display: flex;
  flex-wrap: wrap;
}

.recipes-done-tags span {
  background: rgb(238, 223, 204);
  background: var(--main-bg-color);
  border-radius: 12px;
  font-size: 0.7rem;
  margin: 3px;
  padding: 2px 5px;
}

.alert-mensage {
  background-color: rgba(30, 30, 30, 0.7);
  bottom: 0;
  color: rgb(200, 200, 200);
  font-size: 1rem;
  padding: 5px;
  position: absolute;
  text-align: center;
  width: 100%;
}

.loading-container {
  /* margin: 30% 25px; */
  align-items: center;
  display: flex;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  width: 100vw;
}

.loading-container h4 {
  font-family: 'Lobster', cursive;
  color: #997c30;
  color: var(--main-dourado-color);
  padding-bottom: 50px;
}

.loading-container img {
  width: 70%;
  padding: 0 20px 0 0;
}

.explore-form-button {
  background-color: #997c30;
  background-color: var(--main-dourado-color);
  border: 1px solid #997c30;
  border: 1px solid var(--main-dourado-color);
  border-radius: 10px;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Lobster , cursive;
  font-size: 1.3rem;
  letter-spacing: 1px;
  margin: 10px 0;
  padding: 20px 0;
  width: 80%;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.5);
}

.explore-form-button:disabled {
  background-color: rgb(194, 194, 194);
  border: 1px solid rgb(170, 170, 170);
}

.explore-form-button img {
  width: 38px;
}

.perfil-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.perfil-section img {
  border: 3px solid white;
  border-radius: 10px;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  margin-top: 30px;
  width: 50%;
}

.perfil-section h4 {
  margin: 20px 0;
}

.perfil-section button {
  margin: 8px 0;
  width: 60%;
}

.app-header {
  background-color: rgb(139, 90, 43);
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  padding: 5px;
  position: fixed;
  top: 0;
  width: 100%;
}

.app-header h3 {
  color: white;
  font-family: Lobster , cursive;
  font-size: 1.9rem;
  margin: 3px 0;
}

.header-Items-container {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.search-btn, .profile-btn {
  background-color: inherit;
  border: none;
  height: 30px;
  width: 37px;
}

.filter-main {
  display: flex;
  flex-direction: column;
}

.filter-main input {
  margin: 8px 0 0;
  width: 100%;
}

.filter-container {
  display: flex;
  flex-direction: column;
}

.radio-btn-container {
  color: white;
  display: flex;
  flex-direction: column;
  font-size: 1.2rem;
  font-weight: 600;
  justify-content: space-around;
  margin-top: 10px;
}

.radios-input {
  align-items: center;
  display: flex;
}

.radios-input input {
  height: 20px;
  margin: 0 6px;
  width: 20px;
}

.form-input input {
  border: 1px solid var(--main-dourado-color);
  border-radius: 10px;
  padding: 5px;
}

.form-button {
  background-color: var(--main-dourado-color);
  border: 1px solid var(--main-dourado-color);
  border-radius: 10px;
  color: white;
  font-family: Lobster , cursive;
  font-size: 1.3rem;
  letter-spacing: 1px;
  padding: 5px 0;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.5);
}

.form-button:disabled {
  background-color: rgb(194, 194, 194);
  border: 1px solid rgb(170, 170, 170);
}

.app-footer {
  background-color: rgb(139, 90, 43);
  box-shadow: 0 -2px 3px rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: space-between;
  padding: 5px;
  width: 100%;
}

.app-footer button {
  background-color: inherit;
  border: none;
}

.category-body {
  align-items: stretch;
  display: flex;
  flex-direction: row;
  overflow-y: hidden;
}

/* .category-body {
  display: flex;
  flex-wrap: wrap;
  padding: 4px;
} */

/* .category-body > :nth-child(1) {
  font-size: 1.3rem;
  letter-spacing: 3px;
  width: 100%;
} */

.form-button {
  font-size: 1.1rem;
  height: 35px;
  letter-spacing: 1px;
  margin: 3px;
  padding: 3px 12px;
}

.cardsRecomendations {
  align-items: stretch;
  display: flex;
  flex-direction: row;
  overflow-y: hidden;
}

.buttonStart {
  background-color: var(--main-dourado-color);
  border: 1px solid var(--main-dourado-color);
  border-radius: 10px;
  bottom: 0;
  color: white;
  font-family: Lobster , cursive;
  font-size: 1.3rem;
  left: 15%;
  letter-spacing: 1px;
  padding: 5px 20px;
  position: fixed;
  width: 250px;
}

.share-fav-main {
  display: flex;
  flex-direction: column;
}

.share-fav-container {
  display: flex;
}

.share-fav-container button {
  background-color: var(--main-bg-color);
  border: none;
  height: 40px;
  margin: 0 7px;
  width: 40px;
}

.share-fav-container button img {
  height: 37px;
  width: 37px;
}

.divDetalhes {
  font-size: 20px;
}

.divDetalhes .imgDetalhes {
  height: 250px;

  /* Referência do object-fit:
  https://developer.mozilla.org/pt-BR/docs/Web/CSS/object-fit */
  object-fit: fill;
  width: 100%;
}

.recipeTitle {
  display: flex;
}

.btnFav {
  display: flex;
  justify-content: center;
}

.divDetalhes .pInstruction {
  background-color: rgb(165, 163, 163);
  border-radius: 2%;
  margin-left: 5px;
}

.ingredients {
  align-self: flex-start;
  background-color: rgb(187, 184, 184);
  border-radius: 5%;
  display: flex;
  flex-direction: column;
  margin-left: 5px;
}

.cardsRecomendations {
  align-items: stretch;
  display: flex;
  flex-direction: row;
  margin-bottom: 50px;
  overflow-y: hidden;
}

.alc {
  display: flex;
  justify-content: center;
  margin-right: 10px;
}

.video {
  display: flex;
  margin-bottom: 10px;
  width: 100%;
}


.recipe-in-progress-main {
  display: flex;
  flex-direction: column;
}

.recipe-in-progress-main section {
  margin: 6px;
  padding: 0 10px;
}

.in-progress-header {
  margin-bottom: 10px;
}

.in-progress-header img {
  height: 250px;

  /* Referência do object-fit:
  https://developer.mozilla.org/pt-BR/docs/Web/CSS/object-fit */
  object-fit: fill;
  width: 100%;
}

.in-progress-title-section {
  display: flex;
  justify-content: space-between;
}

h2 {
  color: var(--main-dourado-color);
  font-family: Lobster , cursive;
  margin-bottom: 5px;
}

.in-progress-title-section h5 {
  font-style: italic;
}

.ingredients-section ul {
  background-color: whitesmoke;
  border-radius: 10px;
  color: rgb(80, 80, 80);
  display: flex;
  flex-direction: column;
  font-size: 25px;
  font-weight: 600;
  padding: 15px;
}

.ingredients-section ul label {
  font-style: italic;
}

.ingredients-section ul label input {
  height: 25px;
  margin: 0 5px;
  width: 25px;
}

.ingredients-section li {
  font-size: 1.2rem;
  margin-left: 15px;
  padding: 2px 0;
}

.marked {
  color: var(--main-dourado-color);
  text-decoration: line-through;
}

.insrtuctions-section p {
  background-color: whitesmoke;
  border-radius: 10px;
  color: rgb(80, 80, 80);
  line-height: 1.7rem;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 15px;
}

.in-progress-finish-btn {
  align-self: center;
  background-color: rgb(194, 194, 194);
  border: solid rgb(194, 194, 194);
  border-radius: 30px;
  color: white;
  font-family: Lobster , cursive;
  font-size: 25px;
  margin: 0 15px 10px;
  padding: 15px;
  width: 90%;
}

.finished {
  background-color: green;
  border: solid green;
}

.explore-by-ingredients {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 15px 0 40px;
}

.ingredients-cards {
  align-items: center;
  background-color: white;
  border: none;
  border-radius: 6px;

  /* Referência do box shadow:
  https://www.w3schools.com/css/css3_shadows_box.asp */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2) , 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  display: flex;
  flex-direction: column;
  margin: 0 0 20px;
  width: 150px;
}

.ingredients-cards img {
  width: inherit;
}

.ingredients-cards span {
  color: var(--main-dourado-color);
  font-family: 'Lobster', cursive;
  font-size: 1.5rem;
}

.explorar-origem {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100vw;
}

.explorar-origem select {
  background-color: var(--main-dourado-color);
  border: 1px solid var(--main-dourado-color);
  border-radius: 10px;
  color: white;
  font-weight: 600;
  height: 35px;
  margin: 10px 0 0;
  padding: 5px;
  width: 250px;
}

.login-body {
  align-items: center;
  background-color: var(--main-bg-color);
  display: flex;
  flex-direction: column;
  height: 100vh;

  /* justify-content: start; */
}

.login-body img {
  margin: 50px 0 15px;
  width: 65%;
}

h1 {
  color: rgb(80, 80, 80);
  font-family: Lobster , cursive;
  margin: 0 0 20px;
}

.form-main {
  display: flex;
  flex-direction: column;
}

.form-main > :first-child {
  margin: 10px 0;
}

.form-input {
  width: 100%;
}

.form-main .form-button {
  margin: 10px 0 0;
  width: 100%;
}

.divFav {
  align-items: center;
  background-color: whitesmoke;
  display: flex;
  flex-direction: column;
  font-family: Lobster , sans-serif;
  font-size: 20px;
}

.btnFilters {
  background-color: #f58634;
  border-radius: 10%;
}

.divFav .imgFav {
  border-radius: 10%;
  display: flex;
  height: 250px;
  justify-content: center;

  /* Referência do object-fit:
  https://developer.mozilla.org/pt-BR/docs/Web/CSS/object-fit */
  object-fit: fill;
  width: 100%;
}

.horiTop {
  align-items: center;
  display: flex;
  flex-direction: column;
}

