.cardsRecomendations {
  align-items: stretch;
  display: flex;
  flex-direction: row;
  overflow-y: hidden;
}

.buttonStart {
  background-color: var(--main-dourado-color);
  border: 1px solid var(--main-dourado-color);
  border-radius: 10px;
  bottom: 0;
  color: white;
  font-family: Lobster , cursive;
  font-size: 1.3rem;
  left: 15%;
  letter-spacing: 1px;
  padding: 5px 20px;
  position: fixed;
  width: 250px;
}
